<template>
    <div>
      <h2>小区充值系数修改记录</h2>
      <el-table border :data="tableData" style="width: 98%;margin-top: 15px;">
        <el-table-column type="index" label="序号" width="60px" align="center" ></el-table-column>
        <el-table-column prop="plotName" label="小区名称" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="originFactor" label="原单价（元/吨）" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="updateFactor" label="新单价（元/吨）" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="labor" label="修改人员" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="time" sortable label="时间" min-width="10%" align="center"></el-table-column>
        <el-table-column label="状态" min-width="10%" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 0">待审核</span>
            <span v-else-if="scope.row.state === 1">通过</span>
            <span v-else>失败</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
export default {
  name: 'factorUpdateRecord',
  data () {
    return {
      tableData: [{
        plotName: '荆州新天地',
        originFactor: 0.7,
        updateFactor: 0.9,
        labor: '张三',
        time: '2019-03-21 15:25:23',
        state: 0
      },
      {
        plotName: '荆门新天城',
        originFactor: 0.8,
        updateFactor: 0.6,
        labor: '张一',
        time: '2019-03-21 13:25:23',
        state: 1
      },
      {
        plotName: '荆州碧桂园',
        originFactor: 0.6,
        updateFactor: 0.9,
        labor: '啊哈哈',
        time: '2019-03-22 13:25:23',
        state: 2
      }]
    }
  }
}
</script>

<style scoped>

</style>



